<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div
    class="modal fade"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="AddEdit"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">
            <span v-if="status">新增工單</span><span v-else>修改工單</span>
          </h1>
          <button
            type="button"
            class="btn-close"
            @click="hideModal"
            aria-label="Close"
          ></button>
        </div>
        <VForm
          ref="formRefSignin"
          id="profileForm"
          v-slot="{ meta }"
          @submit="addEditOrder"
        >
          <div class="modal-body">
            <section>
              <!-- 廠商 -->
              <div class="form-floating mb-3">
                <VField
                  name="廠商"
                  type="text"
                  :rules="isCompanyExist"
                  class="form-control"
                  list="datalistOptions"
                />
                <datalist id="datalistOptions">
                  <template v-for="(item, index) in companyList" :key="index">
                    <option :value="item.companyName"></option>
                  </template>
                </datalist>
                <label for="floatingInput"
                  >廠商名稱<ErrorMessage name="廠商" class="text-danger ms-3"
                /></label>
              </div>
              <!-- 施工地址 -->
              <div class="form-floating mb-3">
                <VField
                  name="施工地址"
                  rules="required"
                  type="text"
                  v-model="tempData.address"
                  class="form-control"
                />
                <label for="floatingInput"
                  >施工地址<ErrorMessage
                    name="施工地址"
                    class="text-danger ms-3"
                /></label>
              </div>
              <!-- 施工日期 -->
              <div class="form-floating mb-3">
                <VField
                  name="施工日期"
                  rules="required"
                  type="date"
                  v-model="tempData.constructionDate"
                  class="form-control"
                />
                <label for="floatingInput"
                  >施工日期<ErrorMessage
                    name="施工日期"
                    class="text-danger ms-3"
                /></label>
              </div>
              <!-- 付款方式 -->
              <div class="form-floating mb-3">
                <VField
                  name="付款方式"
                  as="select"
                  rules="required"
                  class="form-control"
                  v-model="tempData.paymentMethod"
                >
                  <option value="現金">現金</option>
                  <option value="支票">支票</option>
                </VField>
                <label for="floatingInput"
                  >付款方式<ErrorMessage
                    name="付款方式"
                    class="text-danger ms-3"
                /></label>
              </div>
              <!-- 總金額 -->
              <div class="form-floating mb-3">
                <VField
                  name="總金額"
                  type="number"
                  rules="required|min_value:0"
                  v-model="tempData.total"
                  class="form-control"
                />
                <label for="floatingInput"
                  >總金額<ErrorMessage name="總金額" class="text-danger ms-3"
                /></label>
              </div>
              <!-- 保留款 -->
              <div class="form-floating mb-3">
                <VField
                  name="保留款"
                  type="number"
                  rules="required|min_value:0"
                  v-model="tempData.retainage"
                  class="form-control"
                />
                <label for="floatingInput"
                  >保留款<ErrorMessage name="保留款" class="text-danger ms-3"
                /></label>
              </div>
            </section>
          </div>
          <div class="modal-footer justify-content-between">
            <button
              type="button"
              class="btn btn-outline-danger"
              v-if="!status"
              @click="deleteOrder(tempData._id)"
            >
              刪除該工單
            </button>
            <div></div>
            <button
              type="submit"
              class="btn btn-primary"
              :disabled="!meta.valid"
            >
              儲存
            </button>
          </div>
        </VForm>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';
import {
  apiGetCompanys,
  apiGetWorkers,
  apiPostOrder,
} from '@/assets/javascript/api';

export default {
  data() {
    return {
      modal: '',
      status: false,
      tempData: {
        address: '',
        company: '',
        status: 0,
        paymentMethod: '',
        total: 0,
        retainage: 0,
        constructionDate: '',
        workers: [],
        transferWorkers: [],
      },
      tempWorker: [],
      companyList: [],
      workerList: [],
    };
  },
  watch: {},
  methods: {
    isCompanyExist(value) {
      this.tempData.company = '';
      let companyId = '';
      // eslint-disable-next-line consistent-return
      this.companyList.forEach((item) => {
        if (item.companyName === value) {
          companyId = item.company;
          // eslint-disable-next-line no-underscore-dangle
          this.tempData.company = item._id;
        }
      });
      return companyId !== '' ? true : '查無此廠商';
    },
    getCompany() {
      apiGetCompanys()
        .then((res) => {
          this.companyList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getWorker() {
      apiGetWorkers()
        .then((res) => {
          this.workerList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    addEditOrder() {
      this.$emitter.emit('loadingStatus', true);
      // eslint-disable-next-line no-underscore-dangle
      if (this.tempData._id) {
        console.log('這是修改');
      } else {
        apiPostOrder(this.tempData)
          .then(() => {
            this.hideModal();
            this.$emitter.emit('getOrder');
            this.$emitter.emit('loadingStatus', false);
          })
          .catch((err) => {
            console.log(err);
            this.$emitter.emit('loadingStatus', false);
          });
      }
    },
    addTransArray() {
      this.tempData.transferWorkers.push({});
    },
    deleteTransArray(index) {
      this.tempData.transferWorkers.splice(index, 1);
    },
    showModal() {
      this.modal.show();
    },
    hideModal() {
      this.tempData = {
        address: '',
        company: '',
        status: 0,
        paymentMethod: '',
        total: 0,
        retainage: 0,
        constructionDate: '',
        workers: [],
        transferWorkers: [],
      };
      this.modal.hide();
    },
  },
  mounted() {
    this.getCompany();
    this.getWorker();
    this.modal = new Modal(this.$refs.AddEdit);
    this.$emitter.on('addEditOrder', (e) => {
      this.status = e.status;
      if (!e.status) {
        this.tempData = JSON.parse(JSON.stringify(e.data));
      }
      this.showModal();
    });
  },
};
</script>
